.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

/* .navbar {
    width: 100%;
    padding: 15px;
} */

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.movies {
  display: grid;
  grid-template-columns: repeat(2, minmax(400px, 1fr));
  grid-gap: 100px;
  padding: 50px;
  width: 80%;
  padding-top: 70px;
}

@media screen and (max-width: 1090px) {
  .movies {
    all: unset;
    display: flex;
    flex-direction: column;
    padding-top: 70px;
    width: 80%;
  }
}
